import React, { Component } from 'react';
import { bool, func, number } from 'prop-types';
import Helmet from 'react-helmet';
import classNames from 'classnames';
import EventListener from 'react-helpful/EventListener';
import Transition from 'react-transition-group/Transition';

export default class Overlay extends Component {
  static propTypes = {
    display: bool.isRequired,
    onCloseRequest: func.isRequired,
    timeout: number,
  };

  static defaultProps = {
    timeout: 375,
  };

  state = {
    display: this.props.display,
  };

  onClick = event => {
    if (event.target === event.currentTarget) {
      this.props.onCloseRequest();
    }
  };

  onKeyUp = event => {
    if (event.key === 'Escape') {
      this.props.onCloseRequest();
    }
  };

  onExited = () => {
    this.setState({ visible: false });
  };

  componentWillReceiveProps({ display }) {
    if (display) {
      this.setState({ visible: true });
    }
  }

  render() {
    const { display, children, className, transition, timeout } = this.props;
    const { visible } = this.state;
    if (!visible) {
      return null;
    }
    return (
      <div className={classNames('overlay', className)} onClick={this.onClick}>
        <Transition in={display} timeout={timeout} classNames={transition} appear={true} onExited={this.onExited}>
          {state => children(classNames(transition, [transition, state].join('--')), { transition: `all ${timeout}ms ease` })}
        </Transition>
        <Helmet>
          <body className="viewer--no-scroll" />
        </Helmet>
        <EventListener
          target={() => document}
          event="keyup"
          on={this.onKeyUp}
        />
      </div>
    );
  }
}



