import React, { Component } from 'react';
import {} from 'prop-types';
import classNames from 'classnames';
import Transition from 'react-transition-group/Transition';

export default class SubscriptionForm extends Component {

  state = {
    email: '',
    valid: false,
  };

  onEmailChange = ({ target: { value } }) => {
    this.setState({ email: value }, () => {
      this.setState({ valid: this.emailInput.validity.valid });
    });
  };

  onConfirm = event => {
    if (event.key === 'Enter' && this.state.valid) {
      this.onSubscribe();
    }
  };

  onSubscribe = () => {
    const { email, valid } = this.state;
    if (valid) {
      this.props.onSubscribe(email);
    }
  };

  renderForm() {
    const { email, valid } = this.state;

    return (
      <div className="subscribe__container">
        <div className="subscribe__title">
          <div className="ribbon">
            <div className="ribbon-stitches-top" />
            <div className="ribbon-content">
              <h1><b>
                Pot of Code<br />
                 <br />
              </b></h1>
            </div>
            <div className="ribbon-stitches-bottom" />
          </div>
        </div>
        <input
          type="email"
          className="subscribe__email"
          placeholder="username@gmail.com"
          value={email}
          ref={emailInput => this.emailInput = emailInput}
          onChange={this.onEmailChange}
          onKeyPress={this.onConfirm}
          autoFocus={true}
          required="required"
        />

        <button
          className="subscribe__button"
          disabled={!valid}
          onClick={this.onSubscribe}
        >
        </button>
        <div className="subscribe__description">
            . !
        </div>

      </div>
    );
  }

  onEntered = () => {
    setTimeout(this.props.onCloseRequest, 500);
  };

  renderConfirmation() {
    return (
      <Transition in={true} appear={true} timeout={375} onEntered={this.onEntered}>
        {state => (
          <div className="subscribe__container">
            <div className={classNames('subscribe__title', `subscribe__title--${state}`)} style={{ transition: `all ${375}ms ease` }}>
              <div className="ribbon">
                <div className="ribbon-stitches-top" />
                <div className="ribbon-content">
                  <h1><b>
                    !
                  </b></h1>
                </div>
                <div className="ribbon-stitches-bottom" />
              </div>
            </div>
          </div>
        )}
      </Transition>
    );
  }

  render() {
    const { className, style, subscribed, onCloseRequest } = this.props;
    return (
      <div className={`subscribe ${className}`} style={style}>
        <div className="subscribe__close" onClick={onCloseRequest}>close</div>
        {
          subscribed
            ? this.renderConfirmation()
            : this.renderForm()
        }
      </div>
    );
  }
}



