import React, { Component } from 'react';
import {} from 'prop-types';
import Overlay from '../UI/Overlay';
import SubscriptionForm from './SubscriptionForm';

function sendPulse(email) {
  const params = {
    callback: 'onSubscribeResponse',
    'sform[email]': email,
    '': '',
    sform_lang: 'ru',
    'sform[hash]': '5282c330c9f7c6a127f710619146a2659e7cdac1364771397b45e43c555ffd02',
    [`sform[${btoa('autoSite')}]`]: 'potofcode.com',
    _: Date.now(),
  };
  const query = Object.entries(params).map(([key, value]) => encodeURIComponent(key) + '=' + encodeURIComponent(value)).join('&');
  const src = `https://login.sendpulse.com/members/forms/jsonp-submit?${query}`;
  const script = document.createElement('script');
  script.src = src;
  return new Promise(resolve => {
    script.onload = () => {
      script.parentNode.removeChild(script);
      resolve();
    };
    document.body.appendChild(script);
  });
}

function mailChimp() {
  return async (EMAIL) => {
    const params = {
      'mc-embedded-subscribe-form[u]': '2086f3a4bc22e651ff2d7721b',
      'mc-embedded-subscribe-form[amp;id]': '731984f7af',
      'mc-embedded-subscribe-form[b_2086f3a4bc22e651ff2d7721b_731984f7af]': '',
      'mc-embedded-subscribe-form[subscribe]': 'Subscribe',
    };

    // https://potofcode.us16.list-manage.com/subscribe/post
  };
}

const displayPeriod = 1000 * 60 * 60 * 24 * 3;

export default class Subscription extends Component {
  state = {
    display: false,
    subscribed: false,
  };

  onSubscribeResponse = (...args) => {
    this.setState({ subscribed: true });
  };

  onSubscribe = email => {
    sendPulse(email);
  };

  onCloseRequest = () => {
    this.setState({ display: false });
    if (typeof localStorage !== 'undefined') {
      localStorage.nextSubscriptionRequest = Date.now() + displayPeriod;
    }
  };

  componentDidMount() {
    window.onSubscribeResponse = this.onSubscribeResponse;
    if (typeof localStorage !== 'undefined') {
      const nextSubscriptionRequest = parseInt(localStorage.nextSubscriptionRequest) || 0;
      if (nextSubscriptionRequest < Date.now()) {
        setTimeout(() => this.setState({ display: true }), 100);
      }
    }
  }

  render() {
    const { display, subscribed } = this.state;
    return (
      <Overlay display={display} onCloseRequest={this.onCloseRequest} transition="slide-up">
        {(transitionClass, style) => <SubscriptionForm className={transitionClass} style={style} subscribed={subscribed} onCloseRequest={this.onCloseRequest} onSubscribe={this.onSubscribe} />}
      </Overlay>
    );
  }
}



